gle': !icon }\" :key=\"icon\">\r\n      <i v-if=\"icon\" class=\"far\" :class=\"icon\" ></i>\r\n    </span>\r\n    <input type=\"radio\" name=\"rad\" v-model=\"checked\" :id=\"model.id\" :value=\"model.id\">        \r\n    <label v-show=\"!edit\" class=\"tree-text\" :class=\"{ 'searched-text': isSearchText }\" :for=\"model.id\" v-on:dblclick=\"dodbclick\" @contextmenu.prevent=\"showContextMenu\" key=\"label\">{{model.text}}</label>    \r\n    <input v-show=\"edit\" ref=\"title\" class=\"tree-text\" v-model=\"model.text\" :placeholder=\"model.text\" key=\"input\" @blur=\"blur\" @keyup.enter=\"blur\">\r\n    <div class=\"tree-children\">\r\n      <ul v-show=\"open\" v-if=\"isFolder\">\r\n        <v-treeview-item v-for=\"child in model.children\" :key=\"child.id\" \r\n        :model=\"child\" :treeTypes=\"treeTypes\" :openAll=\"openAll\" @addNode=\"addNode\"\r\n        @selected=\"selected\" @dodbclick=\"dodbclick\" :searchText=\"searchText\" @openTree=\"openTree\">\r\n        </v-treeview-item>      \r\n      </ul>\r\n    </div>\r\n  </li>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'v-treeview-item',\r\n  props: ['model', 'treeTypes', 'openAll', 'searchText'],\r\n  data() {\r\n    return {\r\n      open: false,\r\n      checked: null,\r\n      edit: false,\r\n      mnode: {},\r\n    }\r\n  },\r\n  computed: {\r\n    isFolder() {\r\n      return this.model.children && this.model.children.length\r\n    },\r\n    icon() {\r\n      return this.getTypeRule(this.model.type).icon\r\n    },\r\n    isSearchText() {\r\n      if (this.searchText && this.searchText != '') {\r\n        if (\r\n          this.model.text\r\n            .toLowerCase()\r\n            .indexOf(this.searchText.toLowerCase()) !== -1\r\n        ) {\r\n          this.openTree(this)\r\n          return true\r\n        } else return false\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    getTypeRule(type) {\r\n      var typeRule = this.treeTypes.filter(t => t.type == type)[0]\r\n      return typeRule\r\n    },\r\n    blur() {\r\n      this.edit = false\r\n    },\r\n    selected(node) {\r\n      this.checked = null\r\n      this.checked = this.model.id\r\n      this.$emit('selected', node)\r\n    },\r\n    dodbclick() {\r\n      this.checked = null\r\n      this.checked = this.model.id\r\n      if (this.isFolder) {\r\n        this.$emit('dodbclick', this)\r\n      } else {\r\n        this.$emit('dodbclick', this)\r\n      }\r\n    },\r\n    openTree(node) {\r\n      this.open = true\r\n      this.$emit('openTree', node)\r\n    },\r\n    addNode(newNode) {\r\n      var typeRule = this.getTypeRule(this.model.type)\r\n\r\n      if (typeRule.valid_children.indexOf(newNode.type) > -1) {\r\n        this.model.children.push(newNode)\r\n      }\r\n    },\r\n    editName() {\r\n      this.edit = true\r\n      this.$nextTick(() => this.$refs.title.focus())\r\n    },\r\n    showContextMenu(e) {\r\n      e.preventDefault()\r\n      this.open = true\r\n      this.selected(this)\r\n    },\r\n    toggle() {\r\n      if (this.isFolder) {\r\n        this.open = !this.open\r\n      }\r\n      this.selected(this)\r\n    }\r\n  },\r\n  created() {\r\n    if (this.model.id == null) {\r\n      this.editName()\r\n    }\r\n    this.open = this.openAll\r\n  },\r\n  watch: {\r\n    openAll(openAll) {\r\n      this.open = openAll\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\nul {\r\n  display: block;\r\n  list-style: none;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\nul .tree-node {\r\n  display: block;\r\n  padding-left: 15px;\r\n}\r\n.toggle-icon {\r\n  display: inline-block;\r\n  width: 20px;\r\n  position: relative;\r\n  z-index: 99;\r\n  cursor: pointer;\r\n}\r\n\r\nul .tree-node :hover:before {\r\n  background: rgba(190, 235, 255, 0.3);\r\n}\r\n\r\nul .tree-node input[type='radio'] {\r\n  display: none;\r\n}\r\nul .tree-node input[type='radio']:checked + label:before {\r\n  background: rgba(83, 215, 220, 0.3);\r\n}\r\n\r\nul label {\r\n  cursor: pointer;\r\n  -webkit-user-select: none;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n}\r\nul label:before {\r\n  -moz-box-sizing: border-box;\r\n  box-sizing: border-box;\r\n  content: '';\r\n  height: 21px;\r\n  left: 0;\r\n  position: absolute;\r\n  right: 0;\r\n}\r\n\r\n.searched-text {\r\n  font-style: italic;\r\n  color: #cc0000;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

